<template>
  <div class="shopbag">
      <div class="tip clearfix">
          <span class="fl three_t_tip">· 满 88 元即可享受包邮</span>
          <!-- <span class="fl three_t_tip">· 已满足 88 元包邮条件</span> -->
          <span class="fr three_t_tip" @click="$router.push('/recommend')">去购买 >></span>
      </div>
      <!-- 购物车列表 -->
      <div class="goodslist">
        <h1 class="large_t">购物袋</h1>
        <div>
          <ul>
            <swipeout>
              <li class="clearfix" v-for="(item,index) in baglist" :key="index">
                  <swipeout-item @on-close="handleEvents('on-close')" @on-open="handleEvents('on-open')" transition-mode="follow">
                    <div slot="right-menu">
                      <swipeout-button @click.native="onButtonClick('delete')" type="warn">删除</swipeout-button>
                    </div>
                    <div slot="content" class="demo-content vux-1px-t">
                        <div class="check fl">
                            <input type="checkbox" :id="item.name" :value="item.name" style="display: none;" @change="checkItem(index)" v-model="checkedNames">
                            <label ref="label" :for="item.name"  class="iconfont" :class="{'icon-gouxuan':item.ischecked,'icon-weixuanzhong':!item.ischecked}"></label>
                        </div>
                        <img class="fl" src="../../assets/test/goods.png" alt="">
                        <div class="count clearfix fl">
                          <h3 class="two_t_h">{{item.name}}</h3>
                          <p class="three_t_m weidao">{{item.kouwei}}</p>
                          <div class="clearfix fl numbers">
                            <span class="fl three_t_tip price">{{item.simgleprice}}</span>
                            <div class="number three_t_m clearfix">
                              <span @click="midify(1,index)" class="iconfont icon-zengjia fr"></span>
                              <input type="tel" class="fr shuliang"  v-model.lazy="item.num" @blur="showTip(item.num,index)">
                              <span @click="midify(0,index)" class="iconfont fr" :class="[item.num==1?'icon-jianshao1':'icon-jianshao']"></span>
                            </div>
                          </div>
                        </div>
                    </div>
                  </swipeout-item>
              </li>
            </swipeout>
          </ul>
          <!-- <span>Checked names: {{ checkedNames }}</span><br>
          <span>asdsd:{{calcle}}</span> -->
        </div>
      </div>
      <!-- 确认余额下单 -->
      <div class="pay clearfix">
        <div class="left clearfix fl">
          <input type="checkbox" value="true" id="all" style="display: none;" @change="unCheck()">
          <label for="all"  class="ischecked iconfont fl" :class="{'icon-weixuanzhong':checkedNames.length==0,'icon-gouxuan':checkedNames.length!=0}"></label>
          <span class="two_t_m fl xuan">已选 ( {{checkedNames.length}} )</span>
          <div class="pricedetail fr">
            <span class="three_t_m">账户余额 -¥10.00</span><br>
            <span class="allprice">¥14.00</span>
          </div>
        </div>
        <span class="order fr" @click="$router.push('/shoppingBag/comfirm')">下单</span>        
      </div>
      <toast :is-show-mask="true" width="10em" type="text" position="middle" :time="1500" v-model="showMsg" text="最多只能添加100件~"></toast>      
  </div>
</template>

<script>
import { Toast, Swipeout, SwipeoutItem, SwipeoutButton } from "vux";

export default {
  name: "Shopbag",
  data() {
    return {
      showMsg: false,
      baglist: [
        {
          img: require("../../assets/test/goods.png"),
          name: "芒果酸奶",
          kouwei: "150g; 芒果味",
          simgleprice: "¥12.00/盒",
          num: 5,
          ischecked: false
        },
        {
          img: require("../../assets/test/goods.png"),
          name: "得乐蛋白五只装礼盒",
          kouwei: "150g; 芒果味",
          simgleprice: "¥12.00/盒",
          num: 2,
          ischecked: false
        },
        {
          img: require("../../assets/test/goods.png"),
          name: "紫薯酸奶",
          kouwei: "150g; 芒果味",
          simgleprice: "¥12.00/盒",
          num: 12,
          ischecked: false
        }
      ],
      checkedNames: [],
      checkedItems: [],
      num: 1
      // calcle: false
    };
  },
  computed: {
    totalNum: function() {}
  },
  methods: {
    // 数量增减
    midify(i, index) {
      if (i == 1) {
        if (this.baglist[index].num == 100) {
          this.showMsg = true;
        } else {
          this.baglist[index].num++;
        }
      } else {
        if (this.baglist[index].num == 1) {
          return false;
        } else {
          this.baglist[index].num--;
        }
      }
    },
    showTip(num, i) {
      if (num > 100) {
        this.showMsg = true;
        this.baglist[i].num = 100;
      } else if (num < 1) {
        this.baglist[i].num = 1;
      }
    },
    // 选中商品
    checkItem(i) {
      this.baglist[i].ischecked = !this.baglist[i].ischecked;
    },
    unCheck() {
      if (this.checkedNames.length != 0) {
        this.checkedNames = [];
        this.baglist.forEach(i => {
          i.ischecked = false;
        });
      } else {
        this.$refs.label.forEach(i => {
          i.click();
        });
      }
    },
    handleEvents(type) {
      console.log("event: ", type);
    },
    onButtonClick(type) {
      alert("on button click " + type);
    }
  },
  components: {
    Toast,
    Swipeout,
    SwipeoutItem,
    SwipeoutButton
  }
};
</script>

<style scoped lang="less">
.shopbag {
  position: relative;
  .tip {
    padding: 0rem 0.27rem 0rem 0.28rem;
    width: 100vw;
    font-size: 0.16rem;
    height: 0.4rem;
    line-height: 0.4rem;
    background-color: #fffeec;
  }
  .goodslist {
    font-size: 0.16rem;
    padding-top: 0.2rem;
    padding-bottom: 1.1rem;
    padding: 0.2rem 0.27rem 0rem 0.28rem;
    li {
      padding: 0.15rem 0rem;
      border-bottom: 0.01rem solid rgba(0, 0, 0, 0.05);
      &:nth-last-child(1) {
        border-bottom: none;
      }
      .check {
        height: 0.8rem;
        label {
          width: 0.22rem;
          height: 0.22rem;
          display: inline-block;
          margin: 0.27rem 0.16rem 0.27rem 0;
          font-size: 0.22rem;
          position: relative;
          z-index: 20;
        }
      }
      img {
        width: 0.8rem;
        height: 0.8rem;
        display: block;
        border-radius: 0.06rem;
        margin-right: 0.15rem;
      }
      .count {
        width: 1.82rem;
        .weidao {
          margin: 0.05rem 0 0.12rem 0;
        }
        .numbers {
          .price {
            height: 0.24rem;
            line-height: 0.24rem;
          }
          .number {
            width: 1.82rem;
            padding-right: 0.08rem;
            .shuliang {
              border: none;
              width: 0.35rem;
              height: 0.21rem;
              text-align: center;
              line-height: 0.21rem;
            }
          }
        }
      }
    }
  }
  .pay {
    width: 100vw;
    height: 0.52rem;
    font-size: 0.16rem;
    position: fixed;
    bottom: 0.52rem;
    left: 0rem;
    background-color: #fff;
    .left {
      width: 2.75rem;
      border-top: 0.01rem solid #f2f2f2;
      padding-left: 0.28rem;
      .ischecked {
        width: 0.22rem;
        height: 0.22rem;
        margin: 0.12rem 0.06rem 0.12rem 0rem;
        font-size: 0.22rem;
      }
      .pricedetail {
        padding-top: 0.05rem;
        padding-right: 0.2rem;
        text-align: right;
        .allprice {
          font-family: PingFangSC-Regular;
          font-size: 0.16rem;
          color: #e7290f;
          letter-spacing: 0;
        }
      }
      .xuan {
        margin: 0.14rem 0rem;
      }
    }
    .order {
      width: 1rem;
      height: 0.525rem;
      text-align: center;
      line-height: 0.51rem;
      background: #e7290f;
      font-family: PingFangSC-Regular;
      font-size: 0.16rem;
      color: #ffffff;
      letter-spacing: 0;
    }
  }
}
</style>



