
import Toast from 'vux/src/components/toast/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


export default {
name: "Shopbag",
data() {
return {
showMsg: false,
baglist: [
{
img: require("../../assets/test/goods.png"),
name: "芒果酸奶",
kouwei: "150g; 芒果味",
simgleprice: "¥12.00/盒",
num: 5,
ischecked: false
},
{
img: require("../../assets/test/goods.png"),
name: "得乐蛋白五只装礼盒",
kouwei: "150g; 芒果味",
simgleprice: "¥12.00/盒",
num: 2,
ischecked: false
},
{
img: require("../../assets/test/goods.png"),
name: "紫薯酸奶",
kouwei: "150g; 芒果味",
simgleprice: "¥12.00/盒",
num: 12,
ischecked: false
}
],
checkedNames: [],
checkedItems: [],
num: 1

};
},
computed: {
totalNum: function() {}
},
methods: {

midify(i, index) {
if (i == 1) {
if (this.baglist[index].num == 100) {
this.showMsg = true;
} else {
this.baglist[index].num++;
}
} else {
if (this.baglist[index].num == 1) {
return false;
} else {
this.baglist[index].num--;
}
}
},
showTip(num, i) {
if (num > 100) {
this.showMsg = true;
this.baglist[i].num = 100;
} else if (num < 1) {
this.baglist[i].num = 1;
}
},

checkItem(i) {
this.baglist[i].ischecked = !this.baglist[i].ischecked;
},
unCheck() {
if (this.checkedNames.length != 0) {
this.checkedNames = [];
this.baglist.forEach(i => {
i.ischecked = false;
});
} else {
this.$refs.label.forEach(i => {
i.click();
});
}
},
handleEvents(type) {
console.log("event: ", type);
},
onButtonClick(type) {
alert("on button click " + type);
}
},
components: {
Toast,
Swipeout,
SwipeoutItem,
SwipeoutButton
}
};



